<template>
    <div>
        <div>{{THIS_TABLE_NAME_}}</div>
        <textarea cols=100 rows=5 v-model="message"></textarea>
        <!--{{this.$store.state.tool1.msg}}-->
        <br>
        <input type="button" value="生成" @click='vueclick'>
        <input type="button" value="清空" @click='vueclick2'>
        <br>
        <textarea cols=100 rows=10 v-model="OUT_ALL" disabled></textarea>
        <br>
        <br>
        <div>建表CREATE语句</div>
        <textarea cols=100 rows=10 v-model="OUT_CREATE" disabled></textarea>
        <br>
        <div>INSERT触发器</div>
        <textarea cols=100 rows=10 v-model="OUT_INSERT" disabled></textarea>
        <br>
        <div>UPDATE触发器</div>
        <textarea cols=100 rows=10 v-model="OUT_UPDATE" disabled></textarea>
        <br>
        <div>DELETE触发器</div>
        <textarea cols=100 rows=10 v-model="OUT_DELETE" disabled></textarea>
    </div>
</template>

<script>
export default {
    data(){
        return{
            THIS_TABLE_NAME_:"无",
            OUT_CREATE:"",
            OUT_INSERT:"",
            OUT_UPDATE:"",
            OUT_DELETE:"",
            OUT_ALL:"",
        }
    },
    computed:{
        message:{
            get(){
                return this.$store.state.tool1.msg
            },
            set(value){
                this.$store.commit('tool1/messageset',value);
            }
        }
    },
    methods:{// methods 属性中定义了当前 Vue 实例所有可用方法
        vueclick(){
            var str = this.$store.state.tool1.msg;
            var TABLE_NAME_=null;
            var NEW_TABLE_NAME_=null;
            var rows = str.split("\n");
            var newStr = "";
            //================建表语句============
            rows.forEach((row) => {
                if(TABLE_NAME_==null){//未获得表名则匹配查询表名
                    if(row.match(/CREATE TABLE DB./)){
                        //截取到DB.之后的部分
                        var temp1 = row.slice((row.match(/CREATE TABLE DB./).index+"CREATE TABLE DB.".length));
                        //获取到表名
                        TABLE_NAME_=temp1.split(" ")[0];
                        if(TABLE_NAME_!=null&&TABLE_NAME_.length>4){
                            NEW_TABLE_NAME_ = TABLE_NAME_+"_LOG";
                            newStr += (row.replace(new RegExp("DB."+TABLE_NAME_),"DB."+NEW_TABLE_NAME_));
                        }
                    }else return true;//如未获得表名则会直接查下一行直至结束
                }else{//已经获取表名
                    var newRow = row;
                    //去掉非空约束
                    if(newRow.match(/ NOT NULL/)){
                        newRow = newRow.replace(/ NOT NULL/,"")
                    }
                    //修改TIMESTAMP定义
                    if(newRow.match(/ TIMESTAMP\(26,6\)/)){
                        newRow = newRow.replace(/ TIMESTAMP\(26,6\)/," TIMESTAMP")
                    }
                    //修改INTEGER定义
                    if(newRow.match(/ INTEGER\(10\)/)){
                        newRow = newRow.replace(/ INTEGER\(10\)/," INTEGER")
                    }
                    //判断是约束语句，则去掉跳过
                    if(newRow.match(/CONSTRAINT /)){
                        return true;
                    }
                    //如是结束的反括号 ); 
                    if(newRow.match(/\);/)){
                        //如之前无逗号，则增加一个逗号
                        if(newStr.substr(newStr.length-1)!=","){
                            newStr+=","
                        }
                        //并增加字段
                        newStr+="\n    LOG_TIME                    TIMESTAMP,\n    OPER_TYPE                   VARCHAR(20)"
                    }
                    //如有表名，则变更表名
                    if(newRow.match(new RegExp("DB."+TABLE_NAME_))){
                        newRow = newRow.replace(new RegExp("DB."+TABLE_NAME_),"DB."+NEW_TABLE_NAME_)
                    }
                    newStr += ("\n"+newRow)
                }
            });
            if(TABLE_NAME_==null){
                window.console.info("未获取到表名");
                alert("未获取到表名");
                return;
            }
            //window.console.info(newStr);
            //====================================
            //===============触发器===============
            rows = newStr.split("\n");
            var fields = [];
            var fieldsFlag;
            //获取全部字段
            rows.forEach((row) => {
                //判断开始字段定义
                if(row.match(/\(/)){
                    if(row.length==1){
                        fieldsFlag = true;
                        return true;
                    }
                }
                //判断字段定义的结束
                if(row.match(/\);/)){
                    fieldsFlag = false;
                    return false;
                }
                if(fieldsFlag){
                    fields.push(row.trim().replace(/\s+/g," ").split(" ")[0]);
                }
            });
            //组装INSERT触发器
            var INSERT = "";
            INSERT+="CREATE TRIGGER DB."+TABLE_NAME_+"_INSERT AFTER INSERT \n";
            INSERT+="   ON DB."+TABLE_NAME_+" REFERENCING NEW AS N FOR EACH ROW MODE DB2SQL INSERT \n";
            INSERT+="       INTO \n";
            INSERT+="           DB."+TABLE_NAME_+"_LOG(\n";
            fields.forEach((field) => {
                //判断不是开始定义的第一个字段，加逗号
                if(INSERT.substr(INSERT.length-2)!="(\n"){
                    INSERT+="               ,";
                }else INSERT+="               ";
                INSERT+=(field+"\n");
            });
            INSERT+="           )\n";
            INSERT+="        VALUES\n";
            INSERT+="           (\n";
            fields.forEach((field) => {
                //判断不是开始定义的第一个字段，加逗号
                if(INSERT.substr(INSERT.length-2)!="(\n"){
                    INSERT+="               ,";
                }else INSERT+="               ";
                if(field=="LOG_TIME"){
                    INSERT+="CURRENT TIMESTAMP\n"
                }else if(field=="OPER_TYPE"){
                    INSERT+="'INSERT'\n"
                }else INSERT+=("N."+field+"\n");
            });
            INSERT+="           );";
            //window.console.info(INSERT);
            //组装UPDATE触发器
            var UPDATE = "";
            UPDATE+="CREATE TRIGGER DB."+TABLE_NAME_+"_UPDATE AFTER UPDATE ON DB."+TABLE_NAME_+" REFERENCING NEW AS N FOR EACH ROW MODE DB2SQL INSERT \n";
            UPDATE+="       INTO \n";
            UPDATE+="           DB."+TABLE_NAME_+"_LOG(\n";
            fields.forEach((field) => {
                //判断不是开始定义的第一个字段，加逗号
                if(UPDATE.substr(UPDATE.length-2)!="(\n"){
                    UPDATE+="               ,";
                }else UPDATE+="               ";
                UPDATE+=(field+"\n");
            });
            UPDATE+="           )\n";
            UPDATE+="        VALUES\n";
            UPDATE+="           (\n";
            fields.forEach((field) => {
                 //判断不是开始定义的第一个字段，加逗号
                if(UPDATE.substr(UPDATE.length-2)!="(\n"){
                    UPDATE+="               ,";
                }else UPDATE+="               ";
                if(field=="LOG_TIME"){
                    UPDATE+="CURRENT TIMESTAMP\n"
                }else if(field=="OPER_TYPE"){
                    UPDATE+="'UPDATE'\n"
                }else UPDATE+=("N."+field+"\n");
            });
            UPDATE+="           );";
            window.console.info(UPDATE);
            //组装DELETE触发器
            var DELETE = "";
            DELETE+="CREATE TRIGGER DB."+TABLE_NAME_+"_DELETE AFTER DELETE ON DB."+TABLE_NAME_+" REFERENCING OLD AS O FOR EACH ROW MODE DB2SQL INSERT \n";
            DELETE+="       INTO \n";
            DELETE+="           DB."+TABLE_NAME_+"_LOG(\n";
            fields.forEach((field) => {
                //判断不是开始定义的第一个字段，加逗号
                if(DELETE.substr(DELETE.length-2)!="(\n"){
                    DELETE+="               ,";
                }else DELETE+="               ";
                DELETE+=(field+"\n");
            });
            DELETE+="           )\n";
            DELETE+="        VALUES\n";
            DELETE+="           (\n";
            fields.forEach((field) => {
                //判断不是开始定义的第一个字段，加逗号
                if(DELETE.substr(DELETE.length-2)!="(\n"){
                    DELETE+="               ,";
                }else DELETE+="               ";
                if(field=="LOG_TIME"){
                    DELETE+="CURRENT TIMESTAMP\n"
                }else if(field=="OPER_TYPE"){
                    DELETE+="'DELETE'\n"
                }else DELETE+=("O."+field+"\n");
            });
            DELETE+="           );";
            //window.console.info(DELETE);
            this.THIS_TABLE_NAME_=TABLE_NAME_;
            this.OUT_CREATE=newStr;
            this.OUT_INSERT=INSERT;
            this.OUT_UPDATE=UPDATE;
            this.OUT_DELETE=DELETE;
            this.OUT_ALL=newStr+"\n"+INSERT+"\n"+UPDATE+"\n"+DELETE
        },
        vueclick2(){
            this.$store.state.tool1.msg="";
            this.THIS_TABLE_NAME_="无";
            this.OUT_CREATE="";
            this.OUT_INSERT="";
            this.OUT_UPDATE="";
            this.OUT_DELETE="";
            this.OUT_ALL=""
        }
    }
}
</script>

